.visually-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.no-scroll {
  overflow: hidden;
}

.favorites {
  width: 100%;
  margin: 0 auto;
}

.fav-container {
  flex-direction: column;
  gap: 40px;
  margin: 0 auto;
  padding-bottom: 100px;
  display: flex;
}

.fav-hero {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

.fav-image {
  width: 100%;
  height: 100%;
  display: flex;
}

.thumb {
  border-radius: 3%;
  overflow: hidden;
}

.block-for-empty {
  flex-direction: column;
  align-items: center;
  gap: 24px;
  margin: 0 auto;
  display: flex;
}

.message-for-empty {
  color: var(--txt-color);
  width: 100%;
  font-size: 14px;
  line-height: 1.28;
}

.fav-category-fltr-btn {
  border: 1px solid var(--gray-color-btn);
  color: var(--gray-color-btn);
  background-color: var(--bg-main);
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  gap: 18px;
  padding: 14px 18px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
  display: flex;
}

.btn-left1 {
  fill: #f8f8f880;
  background-color: #050505;
  border: 1px solid #050505;
}

.fav-category-fltr-btn:hover, .fav-category-fltr-btn:focus {
  background-color: var(--accent);
  color: var(--white_2);
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.recipes-block {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.btn-all-categori {
  color: var(--primary-color-05);
  background-color: var(--bg-main);
  border: 1px solid var(--primary-color-02);
  border-radius: 15px;
  max-width: 176px;
  padding: 14px;
  font-family: Inter;
  font-size: 16px;
  line-height: 1.2;
}

.active_btn {
  color: var(--white);
  background: var(--green);
}

@media screen and (min-width: 320px) {
  .fav-container {
    max-width: 320px;
    padding-left: 5px;
    padding-right: 5px;
  }
}

@media screen and (min-width: 375px) {
  .fav-container {
    max-width: 375px;
    padding-left: 20px;
    padding-right: 20px;
  }

  .fav-image {
    width: 335px;
    height: 516px;
  }

  .message-for-empty {
    width: 335px;
  }
}

.btn-heard {
  color: #0000;
  cursor: pointer;
  border: 1px solid var(--cornflower-color);
  -webkit-text-stroke: 1px #f8f8f84d;
  text-align: center;
  background-color: #0000;
  justify-content: center;
  align-items: center;
  padding: 16px;
  position: relative;
  color: #fbfbfb !important;
  -webkit-text-stroke: 1px #fff !important;
}

.btn-heard-noectiv {
  color: #0000;
  cursor: pointer;
  border: 1px solid var(--cornflower-color);
  -webkit-text-stroke: 1px #f8f8f84d;
  text-align: center;
  background-color: #0000;
  justify-content: center;
  align-items: center;
  padding: 16px;
  position: relative;
}

.scrollable-list-fav {
  width: 100%;
  overflow: hidden;
}

.fav-category-block {
  box-sizing: border-box;
  text-align: left;
  gap: 0 18px;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  overflow-x: scroll;
}

.button-style-favorites {
  justify-content: center;
  gap: 24px;
  margin: 0 auto;
  display: none;
}

.img-favorites {
  margin-bottom: 64px;
}

@media screen and (min-width: 768px) {
  .fav-container {
    gap: 64px;
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  .fav-hero {
    gap: 64px;
  }

  div.cart-favorite {
    width: 224px;
  }

  .fav-image {
    width: 704px;
    height: 356px;
  }

  .recipe-icon-pic {
    width: 97px;
    height: 83px;
  }

  .block-for-empty {
    gap: 32px;
  }

  .message-for-empty {
    width: 481px;
    font-size: 16px;
    line-height: 1.5;
  }

  .recipes-block {
    gap: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .fav-container {
    max-width: 1280px;
    padding: 0 100px 100px;
  }

  div.cart-favorite {
    width: 258px;
  }

  .fav-image {
    width: 1080px;
    height: 356px;
  }
}

